import { AnimatePresence, motion } from "framer-motion";
import React from "react";
import { ToDoItem, ToDoItemProps } from "./ToDoItem";
import { Backdrop } from "./ToDoModal";

export type ToDoItemsProps = {
  tasks: ToDoItemProps["task"][];
  onToDoItemChange: ToDoItemProps["onStateChange"];
};

export const ToDoItems: React.FC<ToDoItemsProps> = (props) => {
  const [selectedTask, setSelectedTask] =
    React.useState<ToDoItemProps["task"]>();
  return (
    <AnimatePresence mode="popLayout">
      {props.tasks.map((t, i) => (
        <ToDoItem
          index={i}
          key={t.id}
          task={t}
          open={selectedTask?.id === t.id}
          onSelect={setSelectedTask}
          onStateChange={(task, completed) => {
            props.onToDoItemChange(task, completed);
          }}
        />
      ))}
    </AnimatePresence>
  );
};
