import React from "react";
import cn from "classnames";
import {
  NiceButton,
  TaskListItem,
  TaskListItemCheck,
  TaskListItemCheckContainer,
  TaskListItemDetails,
  TaskListItemTitle,
} from "./styled";

import { motion, HTMLMotionProps, AnimatePresence } from "framer-motion";
import { Backdrop } from "./ToDoModal";

export type _Task = {
  id: string;
  title: string;
  completed: boolean;
  details?: string;
  newlyAdded?: boolean;
};

export type ToDoItemProps = {
  index: number;
  onStateChange: (task: _Task, completed: _Task["completed"]) => void;
  onSelect?: (task?: _Task) => void;
  task: _Task;
  open?: boolean;
};

export const ToDoItem: React.FC<ToDoItemProps> = ({
  index,
  task,
  open,
  onStateChange,
  onSelect,
}) => {
  return (
    <TaskListItem
      layout
      open={open}
      layoutId={`todo-root-${task.id}`}
      initial="initial"
      animate={task.completed ? "complete" : "incomplete"}
      exit="unmount"
      onDoubleClick={open ? undefined : () => onSelect?.(task)}
      custom={index}
      variants={{
        initial: {
          x: -20,
          opacity: 0,
          scale: 0.98,
        },
        incomplete: (i) => ({
          x: 0,
          opacity: 1,
          scale: 1,
          transition: {
            duration: 0.5,
            delay: (i ?? 0) * 0.05,
            ease: [0, 0.71, 0.2, 1.01],
          },
        }),
        complete: {
          x: 0,
          opacity: 0.5,
          scale: 1,
        },
        unmount: {
          x: 20,
          opacity: 0,
          scale: 0.98,
        },
      }}
    >
      <motion.div
        style={{ display: "flex", flexDirection: "column", width: "100%" }}
      >
        <motion.div style={{ display: "flex" }}>
          <TaskListItemCheckContainer layout>
            <TaskListItemCheck
              layout
              onClick={(e) => {
                onStateChange(task, !task.completed);
                e.preventDefault();
                e.stopPropagation();
              }}
              checked={task.completed}
            />
          </TaskListItemCheckContainer>
          <TaskListItemTitle
            layout
            completed={task.completed}
            layoutId={`todo-title-${task.id}`}
          >
            {task.title}
          </TaskListItemTitle>
        </motion.div>
        <AnimatePresence mode="popLayout">
          {open && (
            <TaskListItemDetails
              layout
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
            >
              <motion.div layout>{task.details ?? "-"}</motion.div>
              <motion.div layout style={{ display: "flex", gap: 8 }}>
                <NiceButton onClick={() => onSelect?.(undefined)}>
                  Close
                </NiceButton>
              </motion.div>
            </TaskListItemDetails>
          )}
        </AnimatePresence>
      </motion.div>
    </TaskListItem>
  );
};
